import React from "react";
import {Util} from "../../../utils";

const PopupComponent = (props) => {
  return (
    <>
      {props.data
        ? props.data.map((d) => {
            return (<>
              <a style={{ color: "#779ff0", marginLeft: "35% " }}> {Util.StringUtils.subTypeIdToString(d)}</a>
              <hr></hr>
            </>)
          })
        : null}
    </>
  );
};

export default PopupComponent;
