import React, { useState, useEffect } from "react";
import "./style.css";
import { Row, Col, Text, Button } from "../../common/RowColStyle";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faDirections,
  faPhoneAlt,
  faCheckCircle,
  faShareAlt,
  faUserCheck,
  faCheckSquare,
  faPhoneSlash,
  faFlag,
} from "@fortawesome/free-solid-svg-icons";
import {
  BrowserView,
  MobileView,
  isBrowser,
  isMobile,
} from "react-device-detect";
import Popup from "reactjs-popup";
import "reactjs-popup/dist/index.css";
import PopupComponent from "./PopupComponent";
import moment from "moment";

const Card = (props) => {
  const [badge, setBadge] = useState({
    oxygen: { present: false, subType: [] },
    medicine: { present: false, subType: [] },
    beds: { present: false, subType: [] },
    doctor: { present: false, subType: [] },
    food: { present: false, subType: [] },
  });

  const [available, setAvailable] = useState([]);

  useEffect(() => {
    let arr = [];
    for (let key in props.data.properties.resources) {
      switch (key) {
        case "1":
          setBadge((prevState) => ({
            ...prevState,
            oxygen: {
              present: true,
              subType: props.data.properties.resources[key],
            },
          }));
          arr.push("Oxygen Cylinder New/Refill");
          break;
        case "2":
          setBadge((prevState) => ({
            ...prevState,
            medicine: {
              present: true,
              subType: props.data.properties.resources[key],
            },
          }));
          arr.push("Corona Related Medicines");
          break;
        case "3":
          setBadge((prevState) => ({
            ...prevState,
            beds: {
              present: true,
              subType: props.data.properties.resources[key],
            },
          }));
          arr.push("Corona Beds");
          break;
        case "4":
          setBadge((prevState) => ({
            ...prevState,
            doctor: {
              present: true,
              subType: props.data.properties.resources[key],
            },
          }));
          arr.push("Doctors for Covid related queries");
          break;
        case "5":
          setBadge((prevState) => ({
            ...prevState,
            food: {
              present: true,
              subType: props.data.properties.resources[key],
            },
          }));
          arr.push("Fresh Food for People in Isolation");
          break;
      }
    }
    setAvailable(arr);
  }, [props]);

  return (
    <>
      {console.log("am i called? 3", available)}
      <div class="row">
        <div class="column">
          <div class="card">
            <Row>
              {badge.oxygen["present"] ? (
                <>
                  <Popup
                    trigger={
                      <Button
                        // disabled
                        color={"#659a62"}
                        style={{
                          minHeight: "24px",
                          marginTop: "4px",
                          padding: "5px 10px",
                        }}
                      >
                        <FontAwesomeIcon icon={faCheckCircle} /> &nbsp;Oxygen
                      </Button>
                    }
                    position="top center"
                  >
                    <PopupComponent data={badge.oxygen["subType"]} />
                  </Popup>
                  &nbsp;&nbsp;
                </>
              ) : null}
              {badge.beds["present"] ? (
                <>
                  <Popup
                    trigger={
                      <Button
                        // disabled
                        color={"#659a62"}
                        style={{
                          minHeight: "24px",
                          marginTop: "4px",
                          padding: "5px 10px",
                        }}
                      >
                        <FontAwesomeIcon icon={faCheckCircle} /> &nbsp;Beds
                      </Button>
                    }
                    position="top center"
                  >
                    <PopupComponent data={badge.beds["subType"]} />
                  </Popup>
                  &nbsp;&nbsp;
                </>
              ) : null}
              {badge.food["present"] ? (
                <>
                  <Popup
                    trigger={
                      <Button
                        // disabled
                        color={"#659a62"}
                        style={{
                          minHeight: "24px",
                          marginTop: "4px",
                          padding: "5px 10px",
                        }}
                      >
                        <FontAwesomeIcon icon={faCheckCircle} /> &nbsp;Food
                      </Button>
                    }
                    position="top center"
                  >
                    <PopupComponent data={badge.food["subType"]} />
                  </Popup>
                  &nbsp;&nbsp;
                </>
              ) : null}

              {badge.medicine["present"] ? (
                <>
                  <Popup
                    trigger={
                      <Button
                        // disabled
                        color={"#659a62"}
                        style={{
                          minHeight: "24px",
                          marginTop: "4px",
                          padding: "5px 10px",
                        }}
                      >
                        <FontAwesomeIcon icon={faCheckCircle} /> &nbsp;Medicine
                      </Button>
                    }
                    position="top center"
                  >
                    <PopupComponent data={badge.medicine["subType"]} />
                  </Popup>
                  &nbsp;&nbsp;
                </>
              ) : null}

              {badge.doctor["present"] ? (
                <>
                  <Popup
                    trigger={
                      <Button
                        // disabled
                        color={"#659a62"}
                        style={{
                          minHeight: "24px",
                          marginTop: "4px",
                          padding: "5px 10px",
                        }}
                      >
                        <FontAwesomeIcon icon={faCheckCircle} /> &nbsp;Doctor
                      </Button>
                    }
                    position="top center"
                  >
                    <PopupComponent data={badge.doctor["subType"]} />
                  </Popup>
                  &nbsp;&nbsp;
                </>
              ) : null}

              <Button
                disabled
                color={"#659a62"}
                style={{
                  minHeight: "24px",
                  marginTop: "4px",
                  padding: "5px 10px",
                }}
              >
                <FontAwesomeIcon icon={faCheckCircle} /> &nbsp;Ambulance
              </Button>
            </Row>
            <Row>
              <Col>
                <Text style={{ margin: "10px 0" }} bold={true}>
                  {props.data.properties.name ? (
                    <>{props.data.properties.name}</>
                  ) : (
                    <>Unnamed</>
                  )}
                </Text>
              </Col>
              {props.mod ? null : (
                <Col width="30%">
                  <div style={{ marginLeft: "100%" }}>
                    {" "}
                    <Text
                      sub1={true}
                      style={{ alignItems: "center", justifyContent: "center" }}
                    >
                      <a
                        target="_blank"
                        style={{ color: "#779ff0", fontSize: "30px" }}
                        href={`http://www.google.com/maps/place/${props.data.properties.lat},${props.data.properties.lng}/@${props.data.properties.lat},${props.data.properties.lng},17z`}
                      >
                        <FontAwesomeIcon icon={faDirections} />
                      </a>
                    </Text>
                  </div>
                </Col>
              )}
            </Row>
            <Row>
              <Col width="50%">
                <Text style={{ margin: "10px 0" }} sub={true}>
                  {props.data.properties.address ? (
                    <>{props.data.properties.address}</>
                  ) : null}
                </Text>
              </Col>
            </Row>
            <Row>
              <p>{moment(props.data.createAt).format("ddd Do, MMM yyyy")}</p>
            </Row>
            <br />
            <Row style={{ marginTop: "20px" }}>
              <Col width={"20%"}>
                <Text
                  sub1={true}
                  style={{ alignItems: "center", justifyContent: "center" }}
                >
                  <FontAwesomeIcon icon={faPhoneAlt} /> &nbsp;{" "}
                  <a
                    href={
                      props.data.properties.phone
                        ? `tel:${props.data.properties.phone}`
                        : null
                    }
                    style={{ color: "#779ff0" }}
                  >
                    Call
                  </a>
                </Text>
              </Col>
              &nbsp;&nbsp;&nbsp;
              <Col width={"20%"}>
                <Text
                  sub1={true}
                  style={{ alignItems: "center", justifyContent: "center" }}
                >
                  <FontAwesomeIcon
                    icon={props.mod ? faUserCheck : faShareAlt}
                  />{" "}
                  &nbsp;{" "}
                  {props.mod ? (
                    <Popup
                      trigger={<a style={{ color: "#779ff0" }}> Verify</a>}
                      position="bottom center"
                    >
                      <a style={{ color: "#779ff0" }}>
                        {" "}
                        <FontAwesomeIcon icon={faCheckSquare} /> &nbsp;Verified
                      </a>
                      <hr></hr>
                      <a style={{ color: "#779ff0" }}>
                        {" "}
                        <FontAwesomeIcon icon={faPhoneSlash} />{" "}
                        &nbsp;Unreachable
                      </a>
                      <hr></hr>
                      <a style={{ color: "#779ff0" }}>
                        <FontAwesomeIcon icon={faFlag} /> &nbsp;Fake
                      </a>
                    </Popup>
                  ) : (
                    <a
                      href={
                        isMobile
                          ? `whatsapp://send?text=Availability : ${available.join()} at ${props.data.properties.name}. Contact ${props.data.properties.number} for further details. Courtesy https://sonusood-lite.org`
                          : "https://web.whatsapp.com://send?text= Please Visit http://ad-test.easygov.co.in/PanAdvertisement"
                      }
                      rel="nofollow noopener"
                      target="_blank"
                      className="share-icon"
                      style={{ color: "#779ff0" }}
                    >
                      Share
                    </a>
                  )}
                </Text>
              </Col>
            </Row>
          </div>
        </div>
      </div>
    </>
  );
};

export default Card;
